.swiper-button-next,.swiper-button-prev{
  outline: none !important;
}
.invest-swiper-container {
  padding: 0 30px;
  position: relative;

  .invest-swiper {
    //position: relative;
    padding-bottom: 35px;

    height: 400px;

    .swiper-slide {
      div {
        @media (min-width: 768px) {
          height: 100%;
        }
      }

      .swiper-content {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        text-align: center;
        flex-direction: column;



        h3{
          color: #009432;
        }

        p{
          margin-top: 5px;
          margin-bottom: 25px;
          color: #333333;
        }
      }
      .image-container{
        position: relative;
        a.btn{
          position: absolute;
          right: 30px;
          bottom: 20px;
          i{
            margin-right: 5px;
          }
        }
        img {
          @media (max-width: 767.98px) {
            margin-top: 25px;
          }

          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      a.btn{
        background-color: #009432;
        border-radius: 25px;
        color: #FFFFFF;
        padding:  7px 25px;
        border: 1px solid #009432;
        transition: all ease-in-out .5s;

        &:hover{
          background-color: #FFFFFF;
          color: #009432;
          border: 1px solid #009432;

        }
      }
    }

    .swiper-pagination-bullet-active{
      background: #009432;
    }
  }

  .swiper-button-next {
    right: 0;
  }

  .swiper-button-prev {
    left: 0;
  }

  .swiper-button-next, .swiper-button-prev {
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
  }

  .swiper-button-next:after, .swiper-button-prev:after {
    font-size: 25px;
    color: #009432;
  }

}

.deals-slider-container{
  padding: 0 25px;
  margin-bottom: 25px;
  margin-top: 25px;

  .swiper-slide{
    img{
      max-width: 100%;
      width: 100%;
    }
  }

  .top-thumbs-container{
    position: relative;

    .swiper-slide{
      position: relative;
      .title{
        top: 50%;
        left: 50%;
        position: absolute;
        transform: translate(-50%,-50%);
        text-align: center;
        font-weight: bold;
        font-size: 20px;
        line-height: 1.1;
      }
    }

    .swiper-button-next{
      right: -25px;
    }
    .swiper-button-prev {
      left: -25px;
    }
    .swiper-button-next:after, .swiper-button-prev:after {
      font-size: 25px;
      color: #009432;
    }

  }

  .gallery-bottom{
    margin-top: 30px;

    @media(max-width: 639.98px){
      display: none;
    }
    .swiper-slide{
      max-height: 300px;
      height: 300px;
    }
  }
}
